.nav__toggle {
  position: absolute;
  right: 20px;
  top: -83px;
  width: 22px;
  height: 22px;
  border: none;
  cursor: pointer;
  background: url(../img/icons/icon-close.svg);
  z-index: 4;
}
.nav__list {
  position: absolute;
  top: -102px;
  width: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
}
.nav--close .nav__toggle {
  position: absolute;
  right: 20px;
  top: -83px;
  width: 22px;
  height: 22px;
  border: none;
  cursor: pointer;
  background: url(../img/icons/icon-close.svg);
  z-index: 4;
}
.nav .nav__toggle {
  position: absolute;
  right: 20px;
  top: -83px;
  background: url(../img/icons/icon-menu.svg);
  width: 22px;
  height: 22px;
  border: none;
  cursor: pointer;
}
.nav .nav__list {
  display: none;
}
.nav--close .nav__list {
  position: absolute;
  top: -102px;
  width: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
}
.nav__item:last-child::before {
  position: absolute;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  content: "";
  bottom: 0;
  left: 0;
  @media (min-width: @tablet-width) {
    display: none;
  }
}
.nav__item {
  list-style: none;
  .blue-color();
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.nav__link {
  .font-title();
  display: block;
  padding-top: 17px;
  padding-bottom: 12px;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: @tablet-width) {
  .nav .nav__list {
    margin: 0;
    padding: 0;
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    width: 768px;
    height: 56px;
    align-items: center;
  }
}
@media (min-width: @tablet-width) {
  .nav__toggle {
    display: none;
  }
}
@media (min-width: @tablet-width) {
  .nav__link {
    color: #000000;
    font-size: 14px;
  }
}
@media (min-width: @tablet-width) {
  .nav__item {
    background-color:transparent;
    box-shadow: none;
  }
  .nav__item:nth-child(3){
    grid-column-end: -2;
  }
}
@media (min-width: @desktop-width) {
  .nav .nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    width: 1200px;
    height: 56px;
    align-items: center;
  }
}
