.page-main__logo-container {
  position: relative;
  background-color: #5d8585;
  background-image: url(../img/back-mobile.jpg);
  background-size: cover;
  background-position: 0 bottom;
  background-repeat: no-repeat;

}
.page-main__logo{
  margin: 0 auto;
  display: block;
  padding-top: 52px;
  padding-bottom: 67px;
}
.page-main__title{
  .font-title();
  margin-top: 37px;
}
.page-main__description{
  .font-text();
  color: #333333;
  width: 280px;
  margin: 0 auto;
  margin-bottom: 40px;
}
.page-main {
  border-bottom: 1px solid #E5E5E5;
  @media (min-width: @tablet-width){
    border: none;
  }
}
.page-main__bottom-vector{
  &::before {
    position: absolute;
    background-image: url(../img/vector-right-welcome.svg);
    width: 100%;
    height: 56px;
    background-position: 160px;
    background-repeat: no-repeat;
    content: "";
    bottom: 0;
  }
  &::after {
    position: absolute;
    background-image: url(../img/vector-left-welcome.svg);
    width: 100%;
    height: 56px;
    background-position: 0;
    background-repeat: no-repeat;
    content: "";
    bottom: 0;
  }
}
@media (min-width: @tablet-width){
  .page-main__description{
    line-height: 24px;
    width: 440px;
    margin-bottom: 66px;
  }
}
@media (min-width: @tablet-width){
  .page-main__logo{
    padding-top: 95px;
    padding-bottom: 118px;
    width: 457px;
  }
}
@media (min-width: @tablet-width){
  .page-main__title{
    font-size: 30px;
    margin-top: 66px;
  }
}
@media (min-width: @tablet-width) {
  .page-main__logo-container{
    background-image: url(../img/back-desktop.png);
    background-position: -216px -228px;
    background-size: 1200px 518px;
    background-repeat: no-repeat;
  }
}
@media (min-width: @tablet-width) {
  .page-main__bottom-vector{
    &::before {
      background-image: url(../img/vector-right-tablet.svg);
      background-position: 384px;
    }
    &::after {
      background-image: url(../img/vector-left-tablet.svg);
    }
  }
}
@media (min-width: @desktop-width) {
  .page-main__logo-container{
    background-image: url(../img/back-desktop.png);
    background-position: 0 -225px;
    background-size: 1200px 518px;
    background-repeat: no-repeat;
  }
}
@media (min-width: @desktop-width) {
  .page-main__bottom-vector{
    &::before {
      background-image: url(../img/vector-right-desktop.svg);
      background-position: 600px;
    }
    &::after {
      background-image: url(../img/vector-left-desktop.svg);
    }
  }
}
@media (min-width: @desktop-width) {
  .page-main__title{
  margin-top: 115px;
  }
}
